;\n}\n\n.black {\n  color: black;\n}\n\n@import url('https://fonts.googleapis.com/css2?family=Rowdies&display=swap');\n","@import '@css/useful.scss';\n\n.passwordForm {\n  position: relative;\n  margin: 0 auto;\n  margin-top: 50px;\n  @include desktop {\n    width: 300px;\n  }\n  @include mobile {\n    width: 80%;\n  }\n}\n.passwordForm--forFlex {\n  display: flex;\n}\n.passwordForm--label {\n  display: flex;\n  flex-direction: column;\n  justify-content: end;\n  font-size: $default_small_13;\n  @include desktop {\n    width: 50px;\n  }\n  @include mobile {\n    width: 25%;\n  }\n}\n.passwordForm--input {\n  height: 24px;\n  border: none;\n  border-bottom: 1px solid $darkgrey_default;\n  font-size: $default_small_13;\n  text-align: center;\n  &:focus {\n    outline: none;\n  }\n  @include desktop {\n    width: 200px;\n    margin-left: 10px;\n  }\n  @include mobile {\n    width: 75%;\n  }\n}\n.password,\n.passCheck,\n.email,\n.cert,\n.cert_count {\n  margin-top: 20px;\n}\n.cert {\n  width: 160px;\n}\n.cert_count {\n  margin-left: 0px;\n  width: 40px;\n}\n\n.passwordForm--buttonWrapper {\n  display: flex;\n  justify-content: space-between;\n  padding: 15px 0;\n}\n.passwordForm--error_message {\n  @include medium_kor;\n  color: $pinkred;\n  display: flex;\n  align-items: center;\n  white-space: pre-line\n}\n.passwordForm--button {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n}\n.passwordForm--submitButton {\n  padding: 0;\n  height: 24px;\n  border: none;\n  color: $darkgrey_default;\n  background-color: lavender;\n  padding: 5px;\n  border-radius: 10px;\n  &:hover {\n    cursor: pointer;\n    transform: scale(1.1);\n  }\n}\n.passwordForm--sendButton {\n  padding: 0;\n  height: 24px;\n  border: none;\n  color: $darkgrey_default;\n  background-color: transparent;\n  padding: 5px;\n  border-radius: 10px;\n  &:hover {\n    cursor: pointer;\n    transform: scale(1.1);\n  }\n}\n\n.none {\n  pointer-events: none;\n  color: lightgray;\n  // background-color: lightgray;\n}","@import '@css/fonts.scss';\n\n$basic_width: 700px;\n$desktop: 1024px;\n\n// device mode\n@mixin desktop {\n  @media (min-width: $basic_width) {\n    @content;\n  }\n}\n\n@mixin mobile {\n  @media (max-width: $basic_width) {\n    @content;\n  }\n}\n\n@mixin galaxyFold {\n  @media (max-width: 300px) {\n    @content;\n  }\n}\n\n// utils\n@mixin basic--wrappers {\n  width: $basic_width;\n  margin: 0 auto;\n}\n"],"sourceRoot":""}]);
